
.header{
    display: flex;
    margin: 1rem;
    justify-content: space-between;
    border: 1px solid black;
    
}
.nav-items ul{
  display: flex;
  
}
.nav-items ul li{
  padding: 1.2rem;
  font-size: 1.1rem;
  list-style-type: none;
  color: black;
}
.logo{
    object-fit: cover;
    height: 4rem;
    border-radius: 50%;
    width: auto;
    margin: .4rem;
}
